import { Center, Flex, Text } from "@chakra-ui/react";
import React, { FC } from "react";
import SizesSettingsComponent from "../../../components/SizesSettingsComponent";
import SizesSettingsComponent2 from "../../../components/SizesSettingsComponent2";
import SizesSettingsComponent3 from "../../../components/SizesSettingsComponent3";
import SizesSettingsComponent4 from "../../../components/SizesSettingsComponent4";

const Sizes: FC = (): JSX.Element => {
  const { root, headingStyle, bodyStyle, saveButton } =
    useSettingsStyles();
  return (
    <Flex {...root} flexDir={"column"}>
       <Flex justify={"space-between"} align="flex-end">
        <Flex flexDir={"column"}>
          <Text {...headingStyle}>Sizes</Text>
          <Text {...bodyStyle}>
           Use my sizes filter to personalize your search results.
          </Text>
        </Flex>
          <Center {...saveButton} as="button">
            <Text fontSize={["14px", "12px", "12px", "12px", "14px"]}>
              Save
            </Text>
          </Center>
        </Flex>
        <Flex flexDir={"column"}>
        <SizesSettingsComponent
          head={"Tops"}
          body={""}
        />
        <SizesSettingsComponent2
          head={"Bottoms"}
          body={""}
        />
        <SizesSettingsComponent
          head={"Outwear"}
          body={""}
        />
        {/* <Divider my={"1%"} /> */}
        <SizesSettingsComponent3
          head={"Footwear"}
          body={""}
        />
        <SizesSettingsComponent4
          head={"Tailoring"}
          body={""}
        />
        {/* <Divider my={"1%"} /> */}
        <SizesSettingsComponent
          head={"Accessories"}
          body={""}
        />
      </Flex>
    </Flex>
  );
};

export default Sizes;

export const useSettingsStyles = () => {
  return {
    root: {
      // bg: "green",
      w: "100%",
      h: "100%",
    },
    headingStyle: {
      fontSize: ["14px", "12px", "14px", "16px", "18px"],
    },
    bodyStyle: {
      fontSize: ["14px", "14px", "12px", "12px", "12px"],
      color: "#999999",
    },
    subHeadStyle: {
      fontSize: ["14px", "12px", "12px", "12px", "14px"],
      mt: "4%",
    },
    manageButton: {
      borderWidth: 1,
      borderColor: "#000000",
      px: "2%",
      borderRadius: "100px",
      h: "30px",
    },
     saveButton: {
      borderWidth: 1,
      borderColor: "#000000",
      px: "2%",
      borderRadius: "100px",
      h: "30px",
    },
  };
};
