import { Flex, Spinner, Stack, Text } from "@chakra-ui/react";
import React, { lazy, Suspense, useState } from "react";
import MyProfile from "../MyProfile";
import Notifications from "./SettingsPages/Notifications";
import Security from "./SettingsPages/Security";
import Sizes from "./SettingsPages/Sizes";
import Payments from "./SettingsPages/Payments";
import Container from "../../components/Container";
import { nanoid } from "@reduxjs/toolkit";

// const NavBar = lazy(() => import("../../components/NavBar"));
const NewNavBar = lazy(() => import("../../components/NewNavBar"));

const Settings = (): JSX.Element => {
  const { root, rootBox, leftSection, rightSection, singleRouteStyle } =
    useStyles();
  const [selectedRouteIndex, setSelectedRouteIndex] = useState<number>(0);

  const settings_routes = [
    {
      route: "Edit Profile",
      view: (
        <Suspense key="1" fallback={<Spinner size="sm" />}>
          <MyProfile />
        </Suspense>
      ),
    },
    {
      route: "Sizes",
      view: (
        <Suspense key="2" fallback={<Spinner size="sm" />}>
          <Sizes />
        </Suspense>
      ),
    },
    {
      route: "Security",
      view: (
        <Suspense key="3" fallback={<Spinner size="sm" />}>
          <Security />
        </Suspense>
      ),
    },
    {
      route: "Addresses",
      view: <Flex key="4">Addresses</Flex>,
    },
    {
      route: "Payments",
      view: (
        <Suspense key="5" fallback={<Spinner size="sm" />}>
          <Payments />
        </Suspense>
      ),
    },
    {
      route: "Notifications",
      view: (
        <Suspense key="6" fallback={<Spinner size="sm" />}>
          <Notifications />
        </Suspense>
      ),
    },
    {
      route: "Devices",
      view: <Flex key="7">Devices</Flex>,
    },
  ];

  return (
    <Flex flexDir={"column"}>
      {/* ----------------------------------------------------------------------NAV BAR------------------------------------------------------------ */}
      {/* @ts-ignore */}
      <Suspense fallback={<Spinner size="sm" />}>
        {/* <NavBar borderBottomWidth={"0px"} /> */}
        <NewNavBar borderBottomWidth={"0px"} />
      </Suspense>

      <Container>
        <Stack {...root}>
          <Flex {...rootBox}>
            {/* -----------------------------------LEFT SECTION---------------------------------------------- */}
            <Flex {...leftSection} flexDir={"column"}>
              {settings_routes?.map((e, i) => (
                <Flex
                  key={nanoid()}
                  {...singleRouteStyle}
                  as={"button"}
                  onClick={() => setSelectedRouteIndex(i)}
                  bg={i === selectedRouteIndex ? "white" : "transparent"}
                >
                  <Text fontSize={["14px", "14px", "14px", "14px", "14px"]}>
                    {e.route}
                  </Text>
                </Flex>
              ))}
            </Flex>
            {/* -----------------------------------LEFT SECTION---------------------------------------------- */}
            <Flex {...rightSection} overflowY="scroll">
              {settings_routes?.map(
                (e, i) => i === selectedRouteIndex && e.view
              )}
            </Flex>
          </Flex>
        </Stack>
        `
      </Container>
    </Flex>
  );
};

export default Settings;

const useStyles = () => {
  return {
    root: {
      w: "100%",
      // px: "10%",
      // px: [5, 5, 10, 20, 40],
      h: "600px",
      mt: "108px",
      pt: "3%",
      overflow: "hidden",
      // bg: "green",
    },
    rootBox: {
      w: "100%",
      h: "100%",
      borderRadius: "15px",
      overflow: "hidden",
      borderWidth: "1px",
      borderColor: "#00000007",
    },
    leftSection: {
      h: "100%",
      w: "25%",
      bg: "#F2F2F2",
    },
    rightSection: {
      h: "100%",
      w: "75%",
      p: "3%",

      //   bg: "yellow",
    },
    singleRouteStyle: {
      w: "100%",
      h: "8%",
      //   bg: "red",
      borderBottomWidth: "1px",
      borderBottomColor: "#0000001C",
      align: "center",
      pl: "3%",
    },
  };
};
