import { Center, Flex, Spinner, Switch, Text } from "@chakra-ui/react";
import { FC } from "react";

interface SecuritySettingsComponentProps {
  head: string;
  body: string;
  isButton?: boolean;
  onClick?: (event: Event) => void;
  onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
  loading?: boolean;
  value?: string | number;
  isChecked?: boolean;
  switchLoading?: boolean;
}

const SecuritySettingsComponent: FC<SecuritySettingsComponentProps> = ({
  head,
  body,
  isButton,
  onClick,
  loading,
  onChange,
  value,
  isChecked,
  switchLoading,
}): JSX.Element => {
  const { root, lowerSec, subHeadStyle, headingStyle, bodyStyle, button } =
    useStyles();
  return (
    <Flex {...root} flexDir="column">
      <Text {...subHeadStyle}>{head}</Text>
      <Flex {...lowerSec} flexDir={"row"} justify={"space-between"}>
        <Text {...bodyStyle} w={"65%"}>
          {body}
        </Text>
        {isButton ? (
          //@ts-ignore
          <Center as="button" {...button} onClick={onClick}>
            {loading ? (
              <Spinner mx="18px" my="2px" size={"sm"} />
            ) : (
              <Text fontSize={["14px", "12px", "12px", "12px", "14px"]}>
                Change
              </Text>
            )}
          </Center>
        ) : (
          <Flex align={"center"}>
            {switchLoading && <Spinner size={"xs"} color="dark"></Spinner>}
            <Switch
              sx={{
                "span.chakra-switch__track:not([data-checked])": {
                  backgroundColor: "gray",
                },
                "span.chakra-switch__track": { backgroundColor: "gray.900" },
              }}
              size="sm"
              value={value}
              onChange={onChange}
              pl="5px"
              isChecked={isChecked}
            />
          </Flex>
        )}
      </Flex>
    </Flex>
  );
};

export default SecuritySettingsComponent;

export const useStyles = () => {
  return {
    root: {
      w: "100%",
      mb: "2%",
    },
    lowerSec: {
      w: "100%",
      align: "center",
    },
    headingStyle: {
      fontSize: ["14px", "12px", "14px", "16px", "18px"],
    },
    subHeadStyle: {
      fontSize: ["14px", "12px", "12px", "12px", "14px"],
    },
    bodyStyle: {
      fontSize: ["14px", "14px", "12px", "12px", "12px"],
      color: "#999999",
    },
    button: {
      borderWidth: 1,
      borderColor: "#000000",
      px: "2%",
      py: "0.5%",
      borderRadius: "100px",
    },
  };
};
