import {
  Center,
  Divider,
  Flex,
  Text,
  Modal,
  ModalContent,
  ModalOverlay,
  useDisclosure,
  Avatar,
  Heading,
  Input,
  HStack,
  Spinner,
  InputGroup,
  InputLeftAddon,
  PinInput,
  PinInputField,
  Link,
  Stack,
  Button,
  Spacer,
  useToast,
} from "@chakra-ui/react";
import React, { lazy, FC, useEffect, useState, Suspense } from "react";
import axios from "axios";
import { nanoid } from "@reduxjs/toolkit";

import SecuritySettingsComponent from "../../../components/SecuritySettingsComponent";
import { useDispatch, useSelector } from "react-redux";
import { AuthReducerProps, RegisterFormikValues, UserReducerProps } from "../../../interfaces";
import { RootState } from "../../../redux/store";
import { useNavigate } from "react-router-dom";
import { BASE_URL } from "../../../config";
import { useGetAllPhoneNumberQuery, useUpdateUserMutation } from "../../../redux/rtkQuery/api";
import { countries } from "../../../constants/countries";
import MeProtocolToast from "../../../hooks/MeProtocolToast";
import { Formik, FormikHelpers, FormikValues } from "formik";
// import MeProtocolFormikButton from "../../../components/MeProtocolFormikButton";
import MeProtocolInput from "../../../components/MeProtocolInput";
import { ResetPasswordSchema } from "../../../constants";
import { resetErrorMsg } from "../../../redux/AuthSlice";
import { Dispatch } from "redux";
import { getUserInfo } from "../../../redux/UserSlice";

const CountrySelector = lazy(() => import("../../../components/CountrySelector"));

const Security: FC = (): JSX.Element => {
  let navigate = useNavigate();
  let toast = useToast();
  const dispatch = useDispatch<Dispatch>();

  const {
    root,
    headingStyle,
    bodyStyle,
    subHeadStyle,
    manageButton,
    countrySelect,
    root2,
    desc,
    basedText,
  } = useSettingsStyles();

  const {
    isOpen: phoneVerifyModalIsOpen,
    onOpen: phoneVerifyModalOnOpen,
    onClose: phoneVerifyModalOnClose,
  } = useDisclosure();

  const {
    isOpen: passwordChangeModalIsOpen,
    onOpen: passwordChangeModalOnOpen,
    onClose: passwordChangeModalOnClose,
  } = useDisclosure();

  const { user_data }: UserReducerProps = useSelector((state: RootState) => state.userReducer);
  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);
  const [updateUser] = useUpdateUserMutation();

  const [country, setCountry] = useState<string>("Switzerland");
  const [countryMobile, setCountryMobile] = useState<string>("566");
  const [value, setValue] = useState<any>("");
  const [code, setCode] = useState<string>("");
  const [errMsg, setErrMsg] = useState<string>("");
  // const [password, setPassword] = useState<string>("");
  // const [confirmPassword, setConfirmPassword] = useState<string>("");
  const [passwordInputShow, setPasswordInputShow] = useState<boolean>(false);
  const [codeSentLoading, setCodeSentLoading] = useState<boolean>(false);
  const [verifyLoading, setVerifyLoading] = useState<boolean>(false);
  const [login2faLoading, setLogin2faLoading] = useState<boolean>(false);
  const [deposit2faLoading, setDeposit2faLoading] = useState<boolean>(false);
  const [withdraw2faLoading, setWithdraw2faLoading] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [show, setShow] = useState<boolean>(false);
  // const [passwordResetLoading, setPasswordResetLoading] =
  //   useState<boolean>(false);
  const handleClick = () => setShow(!show);

  const { data: phone_numbers } = useGetAllPhoneNumberQuery(undefined, {
    refetchOnFocus: true,
    skip: !token,
  });

  const getGeoInfo = () => {
    axios
      .get("https://ipapi.co/json/")
      .then((response) => {
        let data = response.data;
        setCountry(data.country_name);
        setCountryMobile(data.country_calling_code.replace("+", ""));
        // alert(JSON.stringify(data.country_name));
      })
      .catch((error: any | unknown) => {
        console.log(error);
      });
  };

  useEffect(() => {
    // user_data?.customer?.login_2fa ? setLogin2fa(true) : setLogin2fa(false);

    return () => {};
  }, [user_data]);

  async function SendResetCode() {
    try {
      setCodeSentLoading(true);
      // const data: any = await axios.post(
      //   `${BASE_URL}/users/password/request-phone-code`,
      //   {
      //     phone: phone_numbers?.phones[0]?.phone,
      //   }
      // );
      // console.log(data, "E GOOO???");
      // if (data?.reset) {
      //   passwordChangeModalOnOpen();
      //   MeProtocolToast(toast, "Reset code sent", 5000);
      // }

      const { status }: { status: number } = await axios.get(
        `${BASE_URL}/users/phone/${phone_numbers?.phones[0]?.phone}/${user_data?.customer?.userId}`
      );
      if (status === 200) {
        passwordChangeModalOnOpen();
        MeProtocolToast(toast, "Reset code sent", 5000);
      }
    } catch (e: any | unknown) {
      console.log(e, "SendResetCodeErr");
      setErrMsg(e?.response?.data?.error);
    } finally {
      setCodeSentLoading(false);
    }
  }

  async function VerifyPhoneCode() {
    if (code.length < 5) return;
    setVerifyLoading(true);
    try {
      const { data }: any = await axios.post(`${BASE_URL}/users/phone/verify`, {
        userId: user_data?.customer?.userId,
        phone: phone_numbers?.phones[0]?.phone,
        phoneCode: code,
      });

      if (data?.isVerified?.token) {
        if (data) {
          setPasswordInputShow(true);
        }
      }

      // console.log(data, "YYYUIPOOO")
    } catch (e: any | unknown) {
      // console.log(e?.response?.data?.error, "YYYUIPOOO")
      setErrMsg(e?.response?.data?.error);
    } finally {
      setVerifyLoading(false);
      setCode("");
    }
  }

  useEffect(() => {
    //function is returning all Geography location
    getGeoInfo();
    // console.log(user_data, "ALL DATA FOR USER");
  }, []);

  const countryData = countries?.map((data) => {
    return { id: nanoid(), ...data };
  });

  const flag = countryData.filter((e) => e.name === "234" || e.callingCodes[0] === "234")?.[0];

  const onSubmit = async (
    { password }: FormikValues,
    actions: FormikHelpers<RegisterFormikValues>
  ) => {
    // console.log( window.location.href.substring(window.location.href.indexOf('=') + 1), password)
    setIsLoading(true);
    console.log(token);

    try {
      const { data }: any = await axios.post(`${BASE_URL}/users/password/reset-by-email`, {
        token: token,
        newPassword: password,
      });

      console.log(data, "TTYTYT");
      if (data?.isReset) {
        MeProtocolToast(toast, "Password changed", 6000);
        passwordChangeModalOnClose();
      }
    } catch (e) {
      console.log(e, "ChangePwdErr");
    } finally {
      setIsLoading(false);
    }
  };

  // -----------------------------------------TOGGLE 2FA FN-----------------------------------------
  async function Toggle2FaOn(option: string): Promise<void> {
    if (option === "login") {
      setLogin2faLoading(true);
    } else if (option === "deposit") {
      setDeposit2faLoading(true);
    } else {
      setWithdraw2faLoading(true);
    }
    await updateUser(
      option === "deposit"
        ? { deposit_2fa: true }
        : option === "login"
        ? { ...user_data, login_2fa: true }
        : { withdraw_2fa: true }
    )
      .then(async (data: any) => {
        if (token) dispatch(getUserInfo({ token }));
        console.log(data);
        // MeProtocolToast(toast, "2FA updated successfully", 2000);
        // console.log(data, "E CHANGE OOO");
      })
      .catch((e: any | unknown) => {
        console.log(e, "UOPIO");
      })
      .finally(() => {
        setLogin2faLoading(false);
        setDeposit2faLoading(false);
        setWithdraw2faLoading(false);
      });
  }

  // -----------------------------------------TOGGLE 2FA FN-----------------------------------------
  async function Toggle2FaOff(option: string): Promise<void> {
    if (option === "login") {
      setLogin2faLoading(true);
    } else if (option === "deposit") {
      setDeposit2faLoading(true);
    } else {
      setWithdraw2faLoading(true);
    }
    await updateUser(
      option === "deposit"
        ? { deposit_2fa: false }
        : option === "login"
        ? { login_2fa: false }
        : { withdraw_2fa: false }
    )
      .then(async ({ data }: any) => {
        if (token) dispatch(getUserInfo({ token }));
        MeProtocolToast(toast, "2FA updated successfully", 2000);
        // console.log(data, "E CHANGE OOO");
      })
      .catch((e: any | unknown) => {
        console.log(e, "UOPIO");
      })
      .finally(() => {
        setLogin2faLoading(false);
        setDeposit2faLoading(false);
        setWithdraw2faLoading(false);
      });
  }

  return (
    <Flex {...root} flexDir={"column"}>
      <Text {...headingStyle}>Security</Text>
      <Text {...bodyStyle}>Secure your account by enabling these settings.</Text>
      <Flex flexDir={"column"}>
        <Text {...subHeadStyle}>Two factor authentication</Text>
        <Divider my={"1%"} />
        <SecuritySettingsComponent
          head={"Enable at login"}
          body={`Well send a temporary 6-digit code to your number ending in ${phone_numbers?.phones[0]?.phone.substr(
            phone_numbers?.phones[0]?.phone.length - 3
          )} when you login.`}
          isChecked={user_data?.customer?.login_2fa}
          switchLoading={login2faLoading}
          onChange={() => {
            if (user_data?.customer?.login_2fa) {
              Toggle2FaOff("login");
            } else {
              Toggle2FaOn("login");
            }
          }}
        />
        <SecuritySettingsComponent
          head={"Enable at deposit"}
          body={`Well send a temporary 6-digit code to your number ending in ${phone_numbers?.phones[0]?.phone.substr(
            phone_numbers?.phones[0]?.phone.length - 3
          )} when you want to deposit.`}
          isChecked={user_data?.customer?.deposit_2fa}
          switchLoading={deposit2faLoading}
          onChange={() => {
            if (user_data?.customer?.deposit_2fa) {
              Toggle2FaOff("deposit");
            } else {
              Toggle2FaOn("deposit");
            }
          }}
        />
        <SecuritySettingsComponent
          head={"Enable at withdraw"}
          body={`Well send a temporary 6-digit code to your number ending in ${phone_numbers?.phones[0]?.phone.substr(
            phone_numbers?.phones[0]?.phone.length - 3
          )} when you want to withdraw.`}
          isChecked={user_data?.customer?.withdraw_2fa}
          switchLoading={withdraw2faLoading}
          onChange={() => {
            if (user_data?.customer?.withdraw_2fa) {
              Toggle2FaOff("withdraw");
            } else {
              Toggle2FaOn("withdraw");
            }
          }}
        />
        <SecuritySettingsComponent
          head={"Text message"}
          body={`Well send a temporary 6-digit code to your number ending in 679 when you login.`}
          isButton
          // onClick={() => alert("Text message")}
          // onClick={() => Toggle2FaOn("login")}
        />
        <Text {...subHeadStyle}>Login</Text>
        <Divider my={"1%"} />
        <SecuritySettingsComponent
          head={"Password"}
          body={`Well send a temporary 6-digit code to your number ending in 679 to reset your password`}
          isButton
          loading={codeSentLoading}
          onClick={SendResetCode}
        />

        <Flex justify={"space-between"} align="flex-end">
          <Text {...subHeadStyle}>Phone number</Text>
          <Center as="button" {...manageButton} onClick={phoneVerifyModalOnOpen}>
            <Text fontSize={["14px", "12px", "12px", "12px", "14px"]}>Manage</Text>
          </Center>
        </Flex>
        <Divider my={"1%"} />
      </Flex>
      {phone_numbers?.phones?.map((e: { phone: string }) => (
        <Flex key={nanoid()} align={"center"}>
          <Avatar src={flag?.flag} size={"xs"} />
          <Text fontSize={"12px"} pl="5px">
            {e?.phone}
          </Text>
        </Flex>
      ))}

      {/* ----------------------------------- PHONE VERIFICATION MODAL SECTION HERE ----------------------------- */}
      <Modal
        isCentered
        blockScrollOnMount={true}
        scrollBehavior={"inside"}
        isOpen={phoneVerifyModalIsOpen}
        size={"xl"}
        onClose={phoneVerifyModalOnClose}
        motionPreset="slideInBottom"
        closeOnOverlayClick={false}
      >
        <ModalOverlay />
        <ModalContent
          w={{ base: "90vw", md: "50vw" }}
          // h={"70%"}
          borderRadius={0}
          p={"2%"}
          overflowY={"scroll"}
        >
          <HStack mb="30px">
            <Heading fontSize={"18px"}>Verify your phone</Heading>
          </HStack>
          {/* ----------------------------------------- INPUT COUNTRY CODE OPTION SECTION IS HERE -------------------------- */}
          <Text fontSize={"14px"} mb="8px">
            Location
          </Text>
          <Suspense fallback={<Spinner size="sm" />}>
            <CountrySelector
              value={country}
              onChange={(e: any) => {
                setCountry(e.target.value);
              }}
              {...countrySelect}
            />
          </Suspense>

          {/* ----------------------------------------- PHONE NUMBER OPTION SECTION IS HERE -------------------------- */}
          <Text fontSize={"14px"} mt={"24px"} mb="8px">
            Phone number
          </Text>
          <InputGroup {...root2}>
            <InputLeftAddon
              w={"15%"}
              p={"0"}
              h={"100%"}
              children={
                <CountrySelector
                  disabled
                  isCallingCode
                  value={countryMobile || "234"}
                  {...countrySelect}
                />
              }
            />
            <Input
              h={"100%"}
              borderRadius="0px"
              boxShadow={"none"}
              outline={"none"}
              type="tel"
              placeholder={`${countryMobile ? countryMobile : "(000)"} 0 000 000 000 `}
              value={value}
              onChange={({ target }: any) => {
                setValue(target.value);
                setCountryMobile(target.value.slice(0, 3));
              }}
            />
          </InputGroup>

          <Divider orientation="horizontal" mt="40px" />

          <Flex mt="16px" justifyContent={"center"} alignItems={"center"} flexDir={"column"}>
            <Text fontSize={"14px"} mb={"10px"}>
              Enter the 2-step verification code that we texted to your phone
            </Text>
            {/* -------------------------------------- PIN INPUT VERIFY CODE SECTION -------------------------------------- */}
            <HStack w={"100%"} h={"48px"} justifyContent={"center"} alignItems={"center"}>
              <PinInput
                onComplete={(e) => {
                  setCode(e);
                  setErrMsg("");
                }}
                otp
                placeholder={"*"}
              >
                {[1, 2, 3, 4, 5, 6]?.map(() => (
                  <PinInputField
                    key={nanoid()}
                    bg={"#F6F6F6"}
                    borderRadius={10}
                    borderWidth={0}
                    w={"48px"}
                    h={"100%"}
                  />
                ))}
              </PinInput>
            </HStack>
            {errMsg && (
              <Text color={"red"} mt={"10px"}>
                {errMsg}
              </Text>
            )}
            <Text pt={"10px"}>
              Didnt receive the code?{" "}
              <Link href="#" color={"#256BF2"}>
                {" "}
                Resend code
              </Link>
            </Text>
          </Flex>

          <Divider orientation="horizontal" mt="16px" />

          <Flex mt={"24px"} flexDir={"column"}>
            {/* ---------------------------- THE ADD PHONE BUTTON SECTION ----------------------- */}
            <Center as={"button"} bg={"black"} color={"light"} h={"50px"}>
              <Text>Add phone</Text>
            </Center>

            {/* ---------------------------- THE CANCEL BUTTON SECTION ----------------------- */}
            <Center
              as={"button"}
              bg={"light"}
              color={"dark"}
              h={"50px"}
              border={"1px solid black"}
              onClick={phoneVerifyModalOnClose}
              mt={"8px"}
            >
              <Text>Cancel</Text>
            </Center>
          </Flex>
        </ModalContent>
      </Modal>

      {/* ----------------------------------- PASSWORD CHANGE MODAL SECTION HERE ----------------------------- */}
      <Modal
        isCentered
        blockScrollOnMount={true}
        scrollBehavior={"inside"}
        isOpen={passwordChangeModalIsOpen}
        size={"xl"}
        onClose={passwordChangeModalOnClose}
        motionPreset="slideInBottom"
        closeOnOverlayClick={false}
      >
        <ModalOverlay />
        <ModalContent
          w={{ base: "90vw", md: "50vw" }}
          // h={"70%"}
          borderRadius={0}
          p={"2%"}
          overflowY={"scroll"}
        >
          {passwordInputShow ? (
            <Stack mb="30px">
              <Heading fontSize={"18px"}>Create new password</Heading>
              <Text fontSize={"12px"} color={"#999999"}>
                Please create and validate your new password
              </Text>
              <HStack w={"100%"} h={"180px"}>
                <>
                  <Formik
                    initialValues={{
                      password: "",
                      confirm: "",
                    }}
                    validationSchema={ResetPasswordSchema}
                    // @ts-ignore
                    onSubmit={onSubmit}
                  >
                    {(formik) => (
                      <Stack
                        as={"form"}
                        onChange={() => dispatch(resetErrorMsg())}
                        //@ts-ignore
                        onSubmit={formik.handleSubmit}
                        w={"100%"}
                      >
                        {/* <Heading fontSize={"18px"}>Create new password</Heading>

                        <Text fontSize={"12px"} color={"#999999"}>
                          Please create and validate your new password
                        </Text> */}

                        <Suspense fallback={<Spinner size="sm" />}>
                          <MeProtocolInput
                            name={"password"}
                            type={show ? "text" : "password"}
                            placeholder={"Password"}
                          />
                          <MeProtocolInput
                            name={"confirm"}
                            type={show ? "text" : "password"}
                            placeholder={"Confirm Password"}
                          />
                        </Suspense>
                        <Text
                          as={Button}
                          onClick={handleClick}
                          alignSelf={"flex-end"}
                          {...basedText}
                          p={"0px"}
                          bg={"transparent"}
                          _hover={{ bg: "transparent" }}
                        >
                          {!show ? "Show" : "Hide"}
                        </Text>

                        {/* <Suspense fallback={<Spinner size="sm" />}>
                          <MeProtocolFormikButton
                            isLoading={isLoading}
                            name={"Change password"}
                            // onClick={onSubmit}
                          />
                        </Suspense> */}
                      </Stack>
                    )}
                  </Formik>
                </>
                {/* ndsifdififsisdfi */}
              </HStack>
              <Spacer />
              <Button
                isLoading={false}
                // as={Button}
                bg={"dark"}
                color={"white"}
                h={"40px"}
                borderRadius={0}
                _hover={{ bg: "dark" }}
                // onClick={() => {}}
                // @ts-ignore
                onClick={onSubmit}
                disabled={false}
              >
                <Text>Next</Text>
              </Button>
              <Center
                bg={"light"}
                color={"dark"}
                h={"40px"}
                borderWidth={1}
                mt={"2px"}
                onClick={() => {
                  passwordChangeModalOnClose();
                  setPasswordInputShow(false);
                }}
                as="button"
              >
                <Text>Cancel</Text>
              </Center>
            </Stack>
          ) : (
            <Stack mb="30px">
              <Heading fontSize={"18px"}>Verify your phone</Heading>
              <Text fontSize={"12px"} color={"#999999"}>
                A six-digit code has been sent to your default phone number
              </Text>
              <HStack w={"100%"} h={"60px"}>
                <PinInput
                  onComplete={(e) => {
                    setCode(e);
                    setErrMsg("");
                  }}
                  otp
                  placeholder={"*"}
                >
                  {[1, 2, 3, 4, 5, 6]?.map(() => (
                    <PinInputField
                      key={nanoid()}
                      bg={"grey"}
                      borderRadius={10}
                      borderWidth={0}
                      w={"14%"}
                      h={"100%"}
                    />
                  ))}
                </PinInput>
              </HStack>
              {errMsg && (
                <Text {...desc} color={"red"}>
                  {errMsg}
                </Text>
              )}
              <Spacer />
              <Button
                isLoading={verifyLoading}
                // as={Button}
                bg={"dark"}
                color={"white"}
                h={"40px"}
                borderRadius={0}
                _hover={{ bg: "dark" }}
                onClick={VerifyPhoneCode}
                disabled={code.length < 5}
              >
                <Text>Next</Text>
              </Button>
              <Center
                bg={"light"}
                color={"dark"}
                h={"40px"}
                borderWidth={1}
                mt={"2px"}
                onClick={() => {
                  passwordChangeModalOnClose();
                  setPasswordInputShow(false);
                }}
                as="button"
              >
                <Text>Cancel</Text>
              </Center>
            </Stack>
          )}
        </ModalContent>
      </Modal>
    </Flex>
  );
};

export default Security;

export const useSettingsStyles = () => {
  return {
    root: {
      // bg: "green",
      w: "100%",
      h: "100%",
    },
    root2: {
      w: "100%",
      h: "48px",
      align: "center",
      justify: "center",
      px: "0px",
      bg: "#FFFFFF",
      borderColor: "1px solid rgba(0, 0, 0, 0.08)",
      outline: "none",
      borderRadius: "0px",
    },
    headingStyle: {
      fontSize: ["14px", "12px", "14px", "16px", "18px"],
    },
    bodyStyle: {
      fontSize: ["14px", "14px", "12px", "12px", "12px"],
      color: "#999999",
    },
    subHeadStyle: {
      fontSize: ["14px", "12px", "12px", "12px", "14px"],
      mt: "4%",
    },
    manageButton: {
      borderWidth: 1,
      borderColor: "#000000",
      px: "2%",
      borderRadius: "100px",
      h: "30px",
      // cursor: "pointer",
    },
    countrySelect: {
      w: {
        base: "100%",
        md: "100%",
      },
      bg: "#FFFFFF",
      color: "dark",
      borderColor: "1px solid rgba(0, 0, 0, 0.08)",
      borderWidth: "1px",
      align: "center",
      borderRadius: "0px",
    },
    basedText: {
      // pt: "30px",
      fontSize: {
        base: "14px",
        md: "16px",
      },
    },
    desc: {
      pb: "5px",
      fontSize: {
        base: "15px",
        md: "17px",
      },
      w: {
        base: "100%",
        md: "80%",
      },
    },
  };
};
