import {
  Box,
  HStack,
  Input,
  Text,
  Spacer,
  Avatar,
  Stack,
  Spinner,
  useToast,
  FormControl,
  FormLabel,
  Button,
} from "@chakra-ui/react";
import { FC, lazy, Suspense, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
// import CountrySelector from "../../components/CountrySelector";
import { useDispatch, useSelector } from "react-redux";
//useDispatch,
import { AuthReducerProps, UserReducerProps } from "../../interfaces";
import { RootState } from "../../redux/store";
// import MeProtocolToast from "../../hooks/MeProtocolToast";
// import { getUserInfo } from "../../redux/UserSlice";
// import { useUpdateUserMutation } from "../../redux/rtkQuery/api";
import { Dispatch } from "redux";
import { BASE_URL } from "../../config";
import { useUpdateUserMutation } from "../../redux/rtkQuery/api";
import MeProtocolToast from "../../hooks/MeProtocolToast";
import { getUserInfo } from "../../redux/UserSlice";

const CountrySelector = lazy(() => import("../../components/CountrySelector"));
const MyProfile: FC = (): JSX.Element => {
  const { button, profileFont1, profileFont2, profileFont3, inputStyle } = useStyles();
  const { id } = useParams();
  let toast = useToast();
  const dispatch = useDispatch<Dispatch>();

  const { countrySelect } = useStyles();
  const { user_data }: UserReducerProps = useSelector((state: RootState) => state.userReducer);
  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);
  const [updateUser] = useUpdateUserMutation();
  const [country, setCountry] = useState<string>("United States of America");
  const [file, setFile] = useState<any>();
  const [fileData, setFileData] = useState<any>();
  // const [uploading, setUploading] = useState<boolean>(false);
  const [userUpdateLoading, setUserUpdateLoading] = useState<boolean>(false);

  const [height, setHeight] = useState<string | any>(user_data?.customer?.height);
  const [weight, setWeight] = useState<string | any>(user_data?.customer?.weight);
  const [bio, setBio] = useState<string | any>(user_data?.customer?.bio);
  const [fullName, setFullName] = useState<string | any>(user_data?.customer?.name);

  function handleChange(e: any) {
    console.log(e.target.files);
    setFile(URL.createObjectURL(e.target.files[0]));
    setFileData(e.target.files[0]);
  }

  useEffect(() => {
    // console.log(user_data);

    getGeoInfo();
  }, []);

  const getGeoInfo = () => {
    axios
      .get("https://ipapi.co/json/")
      .then((response) => {
        let data = response.data;
        setCountry(data.country_name);
        // alert(JSON.stringify(data.country_name));
      })
      .catch((error) => {
        console.log(error);
      });
  };

  async function getSignature() {
    return await axios
      .get(`${BASE_URL}/upload/image-signature`, {
        headers: { "x-access-token": token },
      })
      .then(({ data }) => {
        return {
          apiKey: data.apiKey,
          cloudName: data.cloudName,
          signature: data.signature,
          timestamp: data.timestamp,
          upload_preset: data.upload_preset,
        };
      });
  }

  async function uploadImageToCloudinary() {
    const { signature, timestamp, cloudName, apiKey, upload_preset }: any = await getSignature(); // api call to get signature

    const formData = new FormData();
    formData.append("file", fileData);
    formData.append("upload_preset", upload_preset);
    formData.append("api_key", apiKey);
    formData.append("timestamp", timestamp);
    formData.append("signature", signature);

    return axios
      .post(`https://api.cloudinary.com/v1_1/${cloudName}/image/upload`, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then(({ data }) => {
        console.log(data, "pppp");

        return { secure_url: data?.secure_url };
      });
  }

  // ------------------------------------------------------------------------------
  async function UpdateProfile(): Promise<void> {
    setUserUpdateLoading(true);
    if (fileData) {
      const { secure_url }: any = await uploadImageToCloudinary();
      // console.log(secure_url);

      await updateUser({
        fullName,
        profilePicture: secure_url,
        weight,
        height,
        bio,
      })
        .then(({ data }: any) => {
          if (token) dispatch(getUserInfo({ token }));
          MeProtocolToast(toast, "Profile updated successfully", 2000);
          // console.log(data, "E CHANGE OOO");
        })
        .catch((e: any | unknown) => {
          console.log(e, "UPDATeYUSERZRR");
        })
        .finally(() => {
          setUserUpdateLoading(false);
        });
      return;
    }

    await updateUser({
      fullName,
      weight,
      height,
      bio,
    })
      .then(({ data }: any) => {
        if (token) dispatch(getUserInfo({ token }));
        MeProtocolToast(toast, "Profile updated successfully", 2000);
        // console.log(data, "E CHANGE OOO");
      })
      .catch((e: any | unknown) => {
        console.log(e, "UPDATeYUSERZRR");
      })
      .finally(() => {
        setUserUpdateLoading(false);
      });
  }

  return (
    <Stack>
      {/* ----------------------------------------------FIRST SECTION------------------------------------------------------- */}
      <HStack>
        <Box>
          <Text {...profileFont1}>Edit Profile</Text>
        </Box>
        <Spacer />
        <Box
          as="button"
          {...button}
          // onClick={UpdateProfile}
          onClick={UpdateProfile}
        >
          <Text>{userUpdateLoading ? <Spinner size={"xs"} /> : "Save"}</Text>
        </Box>
      </HStack>
      {/* ----------------------------------------------SECOND SECTION------------------------------------------------------- */}
      <HStack py="2%">
        <Avatar
          name={user_data?.customer?.name}
          src={file || user_data?.customer?.profilePicture}
        />
        <Stack>
          <Text {...profileFont2}>{user_data?.customer?.name}</Text>
          <Text {...profileFont3} decoration="underline" as="button">
            <div>
              <label htmlFor="image_uploads" style={{ cursor: "pointer" }}>
                Change profile photo
              </label>
              <input
                type="file"
                id="image_uploads"
                name="image_uploads"
                accept=".jpg, .jpeg, .png"
                style={{ opacity: "0" }}
                onChange={handleChange}
                placeholder="Changle profile photo"
              />
            </div>
          </Text>
        </Stack>
      </HStack>
      {/* ----------------------------------------------SECOND SECTION------------------------------------------------------- */}
      <Stack>
        <HStack w="100%">
          <Stack w="25%">
            <Text {...profileFont3} ml="0">
              Full Name
            </Text>
            <Input
              {...inputStyle}
              placeholder="Enter Full Name"
              value={fullName}
              onChange={({ target }: { target: { value: string } }) => {
                setFullName(target.value);
              }}
              border="1px solid rgba(0, 0, 0, 0.08)"
              _focus={{ borderColor: "dark", boxShadow: "none" }}
            />
          </Stack>
          <Stack w="50%">
            <Text {...profileFont3} ml="0">
              Email
            </Text>

            <Input
              {...inputStyle}
              placeholder="Enter Email"
              isDisabled={true}
              value={user_data?.email}
              border="1px solid rgba(0, 0, 0, 0.08)"
              _focus={{ borderColor: "dark", boxShadow: "none" }}
            />
          </Stack>
        </HStack>
        <HStack py="20px">
          <Stack w="50%">
            <Text {...profileFont3} ml="0">
              Location
            </Text>
            <Suspense fallback={<Spinner size="sm" />}>
              <CountrySelector
                value={country}
                onChange={(e: any) => {
                  // alert(e.target.value);
                  setCountry(e.target.value);
                }}
                {...countrySelect}
              />
            </Suspense>
          </Stack>

          <Stack w="25%">
            <Text {...profileFont3} ml="0">
              Weight
            </Text>
            <Input
              {...inputStyle}
              placeholder="Enter Weight"
              value={weight}
              onChange={({ target }: { target: { value: string } }) => {
                setWeight(target.value);
              }}
              border="1px solid rgba(0, 0, 0, 0.08)"
              _focus={{ borderColor: "dark", boxShadow: "none" }}
            />
          </Stack>
          <Stack w="25%">
            <Text {...profileFont3} ml="0">
              Height
            </Text>
            <Input
              {...inputStyle}
              placeholder="Enter Height"
              value={height}
              onChange={({ target }: { target: { value: string } }) => {
                setHeight(target.value);
              }}
              border="1px solid rgba(0, 0, 0, 0.08)"
              _focus={{ borderColor: "dark", boxShadow: "none" }}
            />
          </Stack>
        </HStack>
        <Stack>
          <Text {...profileFont3} ml="0">
            Bio
          </Text>
          <Input
            {...inputStyle}
            as="textarea"
            h="100px"
            w="100%"
            textAlign={"start"}
            placeholder=" Write your review here. It must be at least 5 characters long."
            noOfLines={5}
            _focus={{ borderColor: "dark", boxShadow: "none" }}
            value={bio}
            onChange={({ target }: { target: { value: string } }) => {
              setBio(target.value);
            }}
            border="1px solid rgba(0, 0, 0, 0.08)"
          />
        </Stack>
      </Stack>
    </Stack>
  );
};

export default MyProfile;

