import { Checkbox, Flex, Text } from "@chakra-ui/react";
import React, { FC } from "react";

interface SizesSettingsComponentProps {
  head: string;
  body: string;
}

const SizesSettingsComponent: FC<SizesSettingsComponentProps> = ({
  head,
  body,
}): JSX.Element => {
  
  const { root, lowerSec, subHeadStyle, bodyStyle } = useStyles();

  return (
    <Flex {...root} flexDir="column">
      <Text {...subHeadStyle} mt={"4%"}>{head}</Text>
      <Flex {...lowerSec} flexDir={"row"} justify={"space-between"} mt={"10px"} w={"90%"}>
        {/* <Text {...bodyStyle} w={"45%"}>
          {body}
        </Text> */}
        <Checkbox spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"}>
            XXS/40
          </Text>
        </Checkbox>
        <Checkbox spacing={"0.5rem"} colorScheme={"gray"} >
          <Text {...bodyStyle} color={"black"}>
            XXS/40
          </Text>
        </Checkbox>

        <Checkbox spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"}>
            XXS/40
          </Text>
        </Checkbox>
        <Checkbox spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"}>
           XXS/40
          </Text>
        </Checkbox>

        <Checkbox spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"}>
            XXS/40
          </Text>
        </Checkbox>

        <Checkbox spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"} >
            XXS/40
          </Text>
        </Checkbox>

        <Checkbox spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"}>
            XXS/40
          </Text>
        </Checkbox>

        <Checkbox spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"}>
           XXS/40
          </Text>
        </Checkbox>
  
      </Flex>
    </Flex>
  );
};

export default SizesSettingsComponent;

export const useStyles = () => {
  return {
    root: {
      w: "100%",
      mb: "2%",
    },
    lowerSec: {
      w: "100%",
      align: "center",
    },
    headingStyle: {
      fontSize: ["14px", "12px", "14px", "16px", "18px"],
    },
    subHeadStyle: {
      fontSize: ["14px", "12px", "12px", "12px", "14px"],
    },
    bodyStyle: {
      fontSize: ["14px", "14px", "12px", "12px", "12px"],
      color: "#999999",
    },
  };
};
