import { Flex, Text, Center, Image } from "@chakra-ui/react";
import React, { FC } from "react";
import Images_Icons from "../../../constants/icons-images";

const Payments: FC = (): JSX.Element => {
  const { root, headingStyle, bodyStyle, subHeadStyle } = useSettingsStyles();
  return (
    <Flex {...root} flexDir={"column"}>
      <Text {...headingStyle}>Payments</Text>
      <Text {...bodyStyle}>
        Use my sizes filter to personalize your search results.
      </Text>
      {/* ----------------------------- Paypal Payment Button ------------------------- */}
      <Flex flexDir={"column"} w={"70%"}>
        <Text {...subHeadStyle}>Paypal</Text>
        <Center as="button" bg='#F6C657' h='62px' color='black' mt={"24px"}>
         <Image src={Images_Icons.paypalIcon} mr={"5px"} />Connect with PayPal
       </Center>
      </Flex>
     
     {/* ----------------------------- Apple Pay Button ------------------------- */}
     <Flex flexDir={"column"} w={"70%"}>
        <Text {...subHeadStyle}>Apple Pay</Text>
        <Center as="button" bg='#000000' h='62px' color='white' mt={"24px"}>
         <Image src={Images_Icons.applepayIcon} mr={"5px"} />Connect with Apple Pay
       </Center>
      </Flex>

     <Flex flexDir={"column"} w={"70%"}>
        <Text {...subHeadStyle}>Credit Card</Text>
        {/* ------------------------------- CREDIT CARD SHOULD BE HERE ----------------------- */}

         {/* -------------------------------------- NEXT BUTTON -------------------------- */}
        <Center as="button" bg='#B3B3B3' h='60px' color='white' mt={"24px"}>Next</Center>

      </Flex>
    </Flex>
  );
};

export default Payments;

export const useSettingsStyles = () => {
  return {
    root: {
      // bg: "green",
      w: "100%",
      h: "100%",
    },
    headingStyle: {
      fontSize: ["14px", "12px", "14px", "16px", "18px"],
    },
    bodyStyle: {
      fontSize: ["14px", "14px", "12px", "12px", "12px"],
      color: "#999999",
    },
    subHeadStyle: {
      fontSize: ["14px", "12px", "12px", "12px", "14px"],
      mt: "4%",
    },
  };
};
